<template>
    <el-container>
        <el-aside
            style="width: 200px"
            class="left-group-aside"
            v-show="$getUserGroupSee()"
        >
            <leftgroup
                :grouplist="groupCounts"
                @callGroup="callbackGroup"
            ></leftgroup>
        </el-aside>
        <el-main>
            <toolbar
                :buttonList="buttonList"
                placeholder=""
                @callFunction="callFunction"
            ></toolbar>
            <!--列表-->
            <el-table
                :data="tenants"
                :row-class-name="$tableRowClassName"
                v-loading="listLoading"
                style="width: 100%"
            >
                <el-table-column type="index" width="80">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column prop="name" label="租户名"></el-table-column>
                <el-table-column
                    prop="username"
                    label="登录名"
                ></el-table-column>
                <el-table-column prop="typename" label="类型"></el-table-column>
                <el-table-column
                    prop="contact"
                    label="负责人"
                ></el-table-column>
                <el-table-column prop="tel" label="联系方式"></el-table-column>
                <el-table-column
                    prop="tmpcount"
                    label="模板检查项"
                ></el-table-column>
                <el-table-column prop="customizecount" label="自定义检查项">
                    <template slot-scope="scope"
                        >{{ scope.row.customizecount }}&nbsp;&nbsp;<el-link
                            icon="el-icon-edit"
                            type="primary"
                            style="cursor: pointer"
                            :underline="false"
                            @click="handleCust(scope.$index, scope.row)"
                            >配置</el-link
                        ></template
                    >
                </el-table-column>
                <el-table-column label="合计检查项">
                    <template slot-scope="scope">{{
                        scope.row.tmpcount + scope.row.customizecount
                    }}</template>
                </el-table-column>

                <el-table-column prop="status" label="账号状态">
                    <template slot-scope="scope">
                        <el-tag
                            :type="
                                scope.row.status == '0' ? 'success' : 'danger'
                            "
                            disable-transitions
                            >{{
                                scope.row.status == 0 ? "正常" : "停用"
                            }}</el-tag
                        >
                    </template>
                </el-table-column>

                <el-table-column
                    label="操作"
                    width="80"
                    v-if="canEdit || canDelete"
                >
                    <template slot-scope="scope">
                        <el-button
                            v-if="canEdit"
                            plain
                            type="warning"
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)"
                            >编辑</el-button
                        >
                        <el-button
                            v-if="canDelete"
                            plain
                            type="danger"
                            size="mini"
                            @click="handleDel(scope.$index, scope.row)"
                            >删除</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>

            <!--编辑界面-->
            <el-dialog
                title="编辑"
                :visible.sync="editFormVisible"
                v-model="editFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="editForm"
                    label-width="120px"
                    :rules="editFormRules"
                    ref="editForm"
                >
                    <el-col :span="12">
                        <el-form-item label="分组" prop="GROUPID">
                            <el-select
                                v-model="editForm.GROUPID"
                                filterable
                                placeholder="请选择"
                                @change="groupChange"
                            >
                                <el-option
                                    v-for="item in groups"
                                    :key="item.GROUPID"
                                    :label="item.groupshortname"
                                    :value="item.GROUPID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="监测对象" prop="SITEID">
                            <el-select
                                v-model="editForm.SITEID"
                                filterable
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="item in selectSites"
                                    :key="item.SITEID"
                                    :label="item.siteshortname"
                                    :value="item.SITEID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>

                    <el-col :span="12">
                        <el-form-item label="租户名称" prop="name">
                            <el-input
                                v-model="editForm.name"
                                auto-complete="off"
                                maxlength="20"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="登录名" prop="username">
                            <el-input
                                v-model="editForm.username"
                                auto-complete="off"
                                maxlength="20"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="类型" prop="TTID">
                            <el-select
                                v-model="editForm.TTID"
                                filterable
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="item in selectTenantType"
                                    :key="item.TTID"
                                    :label="item.name"
                                    :value="item.TTID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="门牌号" prop="doorplate">
                            <el-input
                                v-model="editForm.doorplate"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="负责人" prop="contact">
                            <el-input
                                v-model="editForm.contact"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="联系方式" prop="tel">
                            <el-input
                                v-model="editForm.tel"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="账号状态" prop="status">
                            <el-select
                                v-model="editForm.status"
                                placeholder="请选择"
                            >
                                <el-option label="正常" :value="0"></el-option>
                                <el-option label="停用" :value="1"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-form>
                <div
                    style="padding-top: 300px"
                    slot="footer"
                    class="dialog-footer"
                >
                    <el-button @click.native="editFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="editSubmit"
                        :loading="editLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>

            <!--新增界面-->
            <el-dialog
                title="新增"
                :visible.sync="addFormVisible"
                v-model="addFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="addForm"
                    label-width="120px"
                    :rules="addFormRules"
                    ref="addForm"
                >
                    <el-col :span="12">
                        <el-form-item label="分组" prop="GROUPID">
                            <el-select
                                v-model="addForm.GROUPID"
                                filterable
                                placeholder="请选择"
                                @change="groupChange"
                            >
                                <el-option
                                    v-for="item in groups"
                                    :key="item.GROUPID"
                                    :label="item.groupshortname"
                                    :value="item.GROUPID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="站点" prop="SITEID">
                            <el-select
                                v-model="addForm.SITEID"
                                filterable
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="item in selectSites"
                                    :key="item.SITEID"
                                    :label="item.siteshortname"
                                    :value="item.SITEID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>

                    <el-col :span="12">
                        <el-form-item label="租户名称" prop="name">
                            <el-input
                                v-model="addForm.name"
                                auto-complete="off"
                                maxlength="20"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="登录名" prop="username">
                            <el-input
                                v-model="addForm.username"
                                auto-complete="off"
                                maxlength="20"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="类型" prop="TTID">
                            <el-select
                                v-model="addForm.TTID"
                                filterable
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="item in selectTenantType"
                                    :key="item.TTID"
                                    :label="item.name"
                                    :value="item.TTID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="门牌号" prop="doorplate">
                            <el-input
                                v-model="addForm.doorplate"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="负责人" prop="contact">
                            <el-input
                                v-model="addForm.contact"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="联系方式" prop="tel">
                            <el-input
                                v-model="addForm.tel"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="账号状态" prop="status">
                            <el-select
                                v-model="addForm.status"
                                placeholder="请选择"
                            >
                                <el-option label="正常" :value="0"></el-option>
                                <el-option label="停用" :value="1"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-form>
                <div
                    style="padding-top: 300px"
                    slot="footer"
                    class="dialog-footer"
                >
                    <el-button @click.native="addFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="addSubmit"
                        :loading="addLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>

            <!--自定义检查项界面-->
            <el-dialog
                title="编辑"
                :visible.sync="custFormVisible"
                v-model="custFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="custForm"
                    label-width="120px"
                    :rules="custFormRules"
                    ref="custForm"
                >
                    <el-row>
                        <el-col
                            :span="24"
                            v-if="
                                custForm.conditions &&
                                custForm.conditions.length
                            "
                        >
                            <el-row
                                v-for="(
                                    condition, index
                                ) in custForm.conditions"
                                :key="index"
                            >
                                <el-col :span="19">
                                    <el-form-item
                                        label="检查项配置"
                                        :key="index"
                                        :prop="'conditions.' + index + '.value'"
                                        :rules="{
                                            required: true,
                                            message: '检查项不能为空',
                                            trigger: 'blur',
                                        }"
                                    >
                                        <el-input
                                            v-model="condition.value"
                                        ></el-input>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="5">
                                    <i
                                        class="el-icon-delete"
                                        style="
                                            cursor: pointer;
                                            line-height: 40px;
                                            margin-left: 2px;
                                        "
                                        @click.prevent="
                                            removeCondition(condition)
                                        "
                                        >删除</i
                                    >

                                    <i
                                        class="el-icon-bottom"
                                        style="
                                            cursor: pointer;
                                            margin-left: 2px;
                                        "
                                        @click.prevent="clickDown(index)"
                                        v-if="
                                            index !=
                                            custForm.conditions.length - 1
                                        "
                                    ></i>
                                    <i
                                        class="el-icon-top"
                                        style="
                                            cursor: pointer;
                                            margin-left: 2px;
                                        "
                                        @click.prevent="clickUp(index)"
                                        v-if="index != 0"
                                    ></i>
                                </el-col>
                            </el-row>
                        </el-col>
                        <el-col
                            ><el-form-item label="">
                                合计：{{ this.custForm.conditions.length }} 项
                                &nbsp;&nbsp;<i
                                    style="color: #02a7f0; cursor: pointer"
                                    class="el-icon-remove"
                                    @click.prevent="removeAllCondition()"
                                    >清空</i
                                >
                            </el-form-item></el-col
                        >
                        <el-col>
                            <el-form-item>
                                <el-button @click="addCondition"
                                    >添加检查项</el-button
                                >
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row class="form-button">
                        <el-col :span="24">
                            <el-button @click.native="custFormVisible = false"
                                >取消</el-button
                            >
                            <el-button
                                type="primary"
                                @click.native="custSubmit"
                                :loading="custLoading"
                                >提交</el-button
                            >
                        </el-col>
                    </el-row>
                </el-form>
            </el-dialog>
        </el-main>
    </el-container>
</template>
<script>
import {
    getTenantList,
    getTenantTypeList,
    addTenant,
    getTenantGroups,
    editTenant,
    checkusername,
    getCustom,
    setCustom,
    getTenantSites,
} from "../../api/api";
import leftgroup from "../../components/LeftGroup";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import dicmixin from "../../assets/dic/dicmixin";
export default {
    components: { leftgroup, Toolbar },
    mixins: [dicmixin],
    data() {
        return {
            groupCounts: [],
            buttonList: [],
            filters: {
                name: "",
            },
            tenants: [],
            total: 0,
            page: 1,
            pageSize: 10,
            listLoading: false,
            addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            custFormVisible: false,
            editLoading: false,
            custLoading: false,
            custFormRules: {},
            editFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择站点",
                        trigger: "blur",
                    },
                ],
                name: [
                    {
                        required: true,
                        message: "请填写租户名称",
                        trigger: "blur",
                    },
                ],
                username: [
                    {
                        required: true,
                        message: "请填写登录名",
                        trigger: "blur",
                    },
                    {
                        message: "登录名不能重复",
                        trigger: "blur",
                        validator: async (rule, value, callback) => {
                            var result = await checkusername({
                                username: this.addForm.username,
                            });
                            if (result.data) {
                                callback();
                            } else {
                                callback(new Error(""));
                            }
                        },
                    },
                ],
                TTID: [
                    {
                        required: true,
                        message: "请选择类型",
                        trigger: "blur",
                    },
                ],
                doorplate: [
                    {
                        required: true,
                        message: "请填写门牌号",
                        trigger: "blur",
                    },
                ],
                contact: [
                    {
                        required: true,
                        message: "请填写负责人",
                        trigger: "blur",
                    },
                ],
                tel: [
                    {
                        required: true,
                        message: "请填写联系方式",
                        trigger: "blur",
                    },
                ],
                status: [
                    {
                        required: true,
                        message: "请选择账号状态",
                        trigger: "blur",
                    },
                ],
            },
            editForm: {},
            custForm: { TEID: 0, conditions: [{ value: "" }] },
            addFormVisible: false,
            addLoading: false,
            addFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择站点",
                        trigger: "blur",
                    },
                ],
                name: [
                    {
                        required: true,
                        message: "请填写租户名称",
                        trigger: "blur",
                    },
                ],
                username: [
                    {
                        required: true,
                        message: "请填写登录名",
                        trigger: "blur",
                    },
                    {
                        message: "登录名不能重复",
                        trigger: "blur",
                        validator: async (rule, value, callback) => {
                            var result = await checkusername({
                                username: this.addForm.username,
                            });
                            if (result.data) {
                                callback();
                            } else {
                                callback(new Error(""));
                            }
                        },
                    },
                ],
                TTID: [
                    {
                        required: true,
                        message: "请选择类型",
                        trigger: "blur",
                    },
                ],
                doorplate: [
                    {
                        required: true,
                        message: "请填写门牌号",
                        trigger: "blur",
                    },
                ],
                contact: [
                    {
                        required: true,
                        message: "请填写负责人",
                        trigger: "blur",
                    },
                ],
                tel: [
                    {
                        required: true,
                        message: "请填写联系方式",
                        trigger: "blur",
                    },
                ],
                status: [
                    {
                        required: true,
                        message: "请选择账号状态",
                        trigger: "blur",
                    },
                ],
            },
            addForm: {},
            canEdit: false,
            canDelete: false,
            groups: [],
            selectSites: [],
            selectTenantType: [],
            selectGroupId: 0,
        };
    },
    methods: {
        clickUp(index) {
            this.swapArray(this.custForm.conditions, index - 1, index);
        },
        clickDown(index) {
            this.swapArray(this.custForm.conditions, index, index + 1);
        },
        swapArray(arr, index1, index2) {
            arr[index1] = arr.splice(index2, 1, arr[index1])[0];
            return arr;
        },
        removeAllCondition() {
            this.custForm.conditions = [];
        },
        removeCondition(item) {
            var index = this.custForm.conditions.indexOf(item);

            if (index !== -1) {
                this.custForm.conditions.splice(index, 1);
            }
        },
        addCondition() {
            this.custForm.conditions.push({
                value: "",
                key: Date.now(),
            });
        },
        callbackGroup(item) {
            this.selectGroupId = item.GROUPID;
            this.page = 1;
            this.getPageList();
        },
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        getPageList(index) {
            if (index) {
                this.page = index;
            } else {
                this.page = 1;
            }
            let paras = {
                groupid: this.selectGroupId,
                keyword: this.filters.name,
                page: this.page,
                size: this.pageSize,
            };
            this.listLoading = true;
            getTenantList(paras).then((res) => {
                if (res.success) {
                    this.tenants = res.data.data;
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        handleCurrentChange(val) {
            this.getPageList(val);
        },
        getGroupCounts() {
            getTenantGroups().then((res) => {
                if (res.success) {
                    this.groupCounts = res.data;
                }
            });
        },
        getTenantType() {
            let paras = {
                page: 1,
                size: 99999999,
            };
            getTenantTypeList(paras).then((res) => {
                if (res.success) {
                    this.selectTenantType = res.data.data;
                }
            });
        },
        groupChange(val) {
            this.$set(this.addForm, "SITEID", "");
            this.$set(this.editForm, "SITEID", "");
            let paras = { GROUPID: val };
            getTenantSites(paras).then((res) => {
                if (res.success) {
                    this.selectSites = res.data;
                }
            });
            //this.selectSites = this.groupSites;
        },
        //删除
        handleDel(index, r) {},

        //显示自定义检查项界面
        async handleCust(index, r) {
            let row = r;

            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });
                return;
            }
            this.custForm.conditions = [];
            this.custForm.TEID = r.TEID;
            let para = { TEID: row.TEID };

            getCustom(para).then((res) => {
                if (res.success) {
                    res.data.forEach((ii) => {
                        this.custForm.conditions.push({
                            value: ii.name,
                            key: Date.now(),
                        });
                    });
                }
            });

            this.custFormVisible = true;

            //this.custForm = Object.assign({}, row);
        },
        //显示编辑界面
        async handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });
                return;
            }
            this.editFormVisible = true;
            this.groupChange(row.GROUPID);
            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd() {
            this.addFormVisible = true;
            this.addForm = {};
            if (this.$refs["addForm"]) {
                this.$refs["addForm"].clearValidate();
            }
        },
        //自定义检查项提交
        custSubmit: function () {
            this.$refs.custForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.custLoading = true;
                    let para = Object.assign({}, this.custForm);

                    setCustom(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "修改成功",
                                type: "success",
                            });
                            this.$refs["custForm"].resetFields();
                            this.custFormVisible = false;
                            this.getPageList(this.page);
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.custLoading = false;
                    });
                    //});
                }
            });
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.editLoading = true;
                    let para = Object.assign({}, this.editForm);
                    editTenant(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "修改成功",
                                type: "success",
                            });
                            this.$refs["editForm"].resetFields();
                            this.editFormVisible = false;
                            this.getPageList(this.page);
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.editLoading = false;
                    });
                    //});
                }
            });
        },
        //新增
        addSubmit: function () {
            let _this = this;
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.addLoading = true;
                    let para = Object.assign({}, this.addForm);
                    addTenant(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "新增成功",
                                type: "success",
                            });
                            this.$refs["addForm"].resetFields();
                            this.addFormVisible = false;
                            this.getPageList(this.page);
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                        this.addLoading = false;
                    });
                    //});
                }
            });
        },
    },
    mounted() {
        this.getPageList();
        this.getGroupCounts();
        this.getGroups();
        this.getTenantType();

        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>
<style scoped>
.left-group {
    padding: 0px 10px;
}
.el-select {
    width: 100%;
}
</style>


